import { Header } from "antd/lib/layout/layout";
import React, { Component, useState } from "react";
import "../../resource/css/Nav.css";

const Nav = () => {
  const [value, setValue] = useState(0);
  const handleValue = (index) => setValue(index);

  const nav = [
    { id: "home", name: "About Me", link: "/seomseom" },
    { id: "history", name: "History", link: "/seomseom/history" },
    { id: "skill", name: "Skill", link: "/seomseom/skill" },
    { id: "pro", name: "Project", link: "/seomseom/project" },
    { id: "book", name: "Book", link: "/seomseom/book" },
  ];

  return (
    <div className="header">
      <nav className="navbar">
        <a href="/seomseom" className="logo">
          SeomSeom's Page
        </a>
        <ul className="nav-menu">
          {nav.map((data, index) => (
            <li
              className={
                index == value ? "nav-item active " + data.id : "nav-item"
              }
              key={index}
              onClick={() => handleValue(index)}
            >
              <a className={data.id} href={data.link}>
                {data.name}
              </a>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
};

export default Nav;
